import { Filter, filters } from "../filter/Filter";

export enum Optimization {
  MinimalNrIngrediants = 1,
  MaximalResueOfIngredients,
}

export class PlanSettings {
  constructor() {
    this.nrRecipes = 5;
    this.optimization = filters.all()[0];
  }

  public clone(): PlanSettings {
    return Object.create(this);
  }

  public nrRecipes: number;
  public optimization: Filter;
}
