import { RecipeIndex } from './db/RecipeDB';

import { Plan2 } from './model/Plan2';
import { Recipe } from './model/Recipe';

import XLSX from 'xlsx';
import _ from 'lodash';

export function getIngredientMap(recipes: Recipe[]) {
    // Ingredient -> (recipe id -> [amount, unit][])
    let ingredientMap = new Map<string, Map<string, [number, string][]>>();

    recipes.forEach(r => {

        r.getIngredients().forEach(i => {
            let ingredientName = i.getName().toLowerCase();
            let oneIngredientRecord = ingredientMap.get(ingredientName);
            if (!oneIngredientRecord) {
                oneIngredientRecord = new Map()
                ingredientMap.set(ingredientName, oneIngredientRecord);
            }

            let oneRecipeRecord = oneIngredientRecord.get(r.getId()) ?? [];
            oneRecipeRecord.push([i.getAmount(), i.getUnit().toLowerCase()]);
            oneIngredientRecord.set(r.getId(), oneRecipeRecord);
        });
    });

    return ingredientMap;
}

function calculateShoppingList(
    ingredientMap: Map<string, Map<string, [number, string][]>>,
    ingredients: string[]) {
    let ingredientList = ingredients.map(name => {

        let oneIngredientRecord = new Map<string, number>()
        let ingredient = ingredientMap.get(name) ?? new Map<string, [number, string][]>()

        // Group ingredient usages by unit
        for (let oneUsage of ingredient.values()) {
            for (let [amount, unit] of oneUsage) {
                oneIngredientRecord.set(unit,
                    (oneIngredientRecord.get(unit) ?? 0) + amount)
            }
        }   

        return { "name": name, "amounts": oneIngredientRecord }
    });

    let ingredientsRows = []

    for (let { name, amounts } of ingredientList) {
        let line: [string] = [name]
        for (let [unit, amount] of amounts) {
            line.push("" + amount + " " + unit)
        }
        ingredientsRows.push(line)
    }

    return ingredientsRows;
}

// Exports ShoppingList as list of MD lines
export function exportShoppingList(recipeIndex: RecipeIndex, plan: Plan2) {
    // Wan't here the recipes as many times as they appear in the plan
    let recipes = recipeIndex.getAll(plan.getAllRecipeIds(false)); 
    let ingredientMap = getIngredientMap(recipes);
    let c = new Intl.Collator();
    let ingredients = Array.from(ingredientMap.keys()).sort(c.compare);
    let ingredientsRows = calculateShoppingList(ingredientMap, ingredients);

    let res = "";

    for (let row of ingredientsRows) {
        res += "**" + row.shift() + "**";
        res += " - ";
        res += row.join(" + ");
        res += "\n";
    }

    return res;
}

// Exports Plan as XLSX
export function exportPlan(recipeIndex: RecipeIndex, plan: Plan2): Blob {
    // Wan't here the recipes as many times as they appear in the plan
    let recipes = recipeIndex.getAll(plan.getAllRecipeIds(false));
    let uniqueRecipes = new Set(recipes);
    let ingredientMap = getIngredientMap(recipes);
    let ingredients = Array.from(ingredientMap.keys()).sort();

    let wb = XLSX.utils.book_new();

    // ----------------------------------------------------------------------------------------
    // Generate map

    let planRows = _.range(0, plan.getNrBuckets()).map(bid => 
        ["Day #"+(bid+1)].concat(
            recipeIndex.getAll(plan.getRecipeIds(bid)).map(r => r.getTitle())));
    let planWS = XLSX.utils.aoa_to_sheet(planRows);
    XLSX.utils.book_append_sheet(wb, planWS, "Plan");

    // ----------------------------------------------------------------------------------------
    // Calculate shopping list

    let ingredientsRows = calculateShoppingList(ingredientMap, ingredients);
    let ingredientsWS = XLSX.utils.aoa_to_sheet(ingredientsRows);
    XLSX.utils.book_append_sheet(wb, ingredientsWS, "Shopping list");

    // ----------------------------------------------------------------------------------------
    // Generate ingredient map

    const mapHeaderRow = recipes.map(r => r.getTitle())
    mapHeaderRow.unshift("")

    const mapDataRows = ingredients.map(name => {

        let line = [name]

        for (let r of recipes) {
            let ingredient = ingredientMap.get(name) ?? new Map<string, [number, string][]>();
            let recipeIngredient = ingredient.get(r.getId());
            if (recipeIngredient) {
                line.push(recipeIngredient.map(au => "" + au[0] + " " + au[1]).join(", "));
            }
            else {
                line.push("");
            }

        }

        return line;
    });

    let mapRows = [mapHeaderRow].concat(mapDataRows);
    let mapWS = XLSX.utils.aoa_to_sheet(mapRows);
    XLSX.utils.book_append_sheet(wb, mapWS, "Ingredient map");

    // ----------------------------------------------------------------------------------------
    // Generate recipes

    // Recipes must be converted to a set to eliminate duplications
    for (let r of uniqueRecipes) {
        let recipeRows = [
            ["Title", r.getTitle()],
            ["Comment", r.getComment() ?? "-"],
            ["Ready in minutes", r.getReadyInMinutes() ?? "-"],
            ["Source", r.getSource() ?? "-"],
            ["Ingredients:"]
        ];

        for (let i of r.getIngredients()) {
            recipeRows.push([i.getName().toLowerCase(), i.getAmount(), i.getUnit().toLowerCase()])
        }

        let recipeWS = XLSX.utils.aoa_to_sheet(recipeRows);
        XLSX.utils.book_append_sheet(wb, recipeWS, r.getTitle().substr(0,31));
    }

    let bytes = XLSX.write(wb, { bookType: 'xlsx', bookSST: false, type: 'array' });
    return new Blob([bytes], {
        type: 'application/octet-stream'
    });
}
