import React, { FunctionComponent } from 'react';

import { Container, Col, Row } from 'react-bootstrap';
import { Binoculars, Clock, Cart2, ExclamationCircle, ChatRightText } from 'react-bootstrap-icons';

import { Recipe } from '../model/Recipe';

type RecipePopoverProps = {
    recipe: Recipe
}

export const RecipeQuickView: FunctionComponent<RecipePopoverProps> = ({ recipe }) => {

    const c = new Intl.Collator();
    const ingredients = recipe.getIngredients().map(r => r.getName())
        .sort(c.compare)
        .map(n => <Col key={n} sm="auto" className="border mt-1 ml-2 pt-2 pb-2">{n}</Col>)

    const labels = recipe.getLabels()
        .sort(c.compare)
        .map(n => <Col key={n} sm="auto" className="border mt-1 ml-2 pt-2 pb-2">{n}</Col>)

    return (
        <Container className="pl-0 pr-2">
            {recipe.getComment() &&
            <Row className="pb-2">
                <Col sm="auto"><ChatRightText height="18"/></Col>
                <Col sm="auto">{recipe.getComment()}</Col>
            </Row>
            }
            {recipe.getReadyInMinutes() &&
            <Row className="pb-2">
                <Col sm="auto"><Clock height="18" /></Col>
                <Col sm="auto">{recipe.getReadyInMinutes() + " minutes"}</Col>
            </Row>
            }
            {recipe.getSource() &&
            <Row className="pb-2">
                <Col sm="auto"><Binoculars height="18" /></Col>
                <Col sm="auto">{recipe.getSource()}</Col>
            </Row>
            }
            <Row>
                <Col xs={1}>
                    <Cart2 height="18"/>
                </Col>
                <Col>
                <Container fluid className="pl-0">
                    <Row>
                    {ingredients}
                    </Row>
                </Container>
                </Col>
            </Row>
            {recipe.getLabels().length > 0 &&
            <Row>
                <Col xs={1}>
                    <ExclamationCircle height="18"/>
                </Col>
                <Col>
                <Container fluid className="pl-0">
                    <Row>
                    {labels}
                    </Row>
                </Container>
                </Col>
            </Row>}
        </Container>
    )
}
