import React, { FunctionComponent } from 'react';

import { Modal, Form, Button } from 'react-bootstrap';

import { PlanSettings } from '../model/PlanSettings';
import { filters } from '../filter/Filter';

type PlanSettingsDialogProps = {
    planSettings: PlanSettings,
    onClose: () => void,
    onUpdate: (planSettings: PlanSettings) => void
}

export const PlanSettingsDialog: FunctionComponent<PlanSettingsDialogProps> = (props) => {

    const changeNrRecipes = (newNrRecipes: number) => {
        let newSettings = props.planSettings.clone();
        newSettings.nrRecipes = newNrRecipes;
        props.onUpdate(newSettings);
    }

    const changeOptimization = (filterId: string) => {
        let filter = filters.get(filterId);
        if (filter) {
            let newSettings = props.planSettings.clone();
            newSettings.optimization = filter;
            props.onUpdate(newSettings);
        }
    }

    const filterList = filters.all().map(f =>
        <option value={f.id} key={f.id}>{f.name}</option>
    )

    return (
        <Modal show={true} onHide={props.onClose}>
        <Modal.Header closeButton>
                <Modal.Title>Plan settings</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <Form onSubmit={(e) => e.preventDefault()}>
            <Form.Group controlId="nrRecipes">
                <Form.Label>Number of recipes:</Form.Label>
                <Form.Control as="select" value={props.planSettings.nrRecipes}
                    onChange={(e) => changeNrRecipes(parseInt(e.target.value))}>
                    <option key="3">3</option>
                    <option key="4">4</option>
                    <option key="5">5</option>
                    <option key="6">6</option>
                    <option key="7">7</option>
                </Form.Control>
            </Form.Group>
            <Form.Group controlId="optimization">
                <Form.Label>Optimize by:</Form.Label>
                <Form.Control as="select" value={props.planSettings.optimization.id}
                    onChange={(e) => changeOptimization(e.target.value)}>
                    {filterList}
                </Form.Control>
            </Form.Group>
        </Form>
        </Modal.Body>
            <Modal.Footer>
                <Button type="button" variant="secondary" onClick={props.onClose}>Close</Button>
            </Modal.Footer>
        </Modal>
    );
}
