import React, { FunctionComponent } from 'react';

import { Container, Row, Col, Card } from 'react-bootstrap';
import { Popover, OverlayTrigger } from 'react-bootstrap';
import { Trash, Eye } from 'react-bootstrap-icons';

import { RecipeQuickView } from './RecipeQuickView'

import { RecipeIndex } from "../db/RecipeDB";
import { Recipe } from "../model/Recipe";
import { Plan2 } from "../model/Plan2";

import { useDrop, useDrag } from 'react-dnd'
import _ from 'lodash';

type RecipeBoxProps = {
    bucketId: number,
    recipe: Recipe,
    onDelete: (recipeId: string) => void
}

const RecipeBox: FunctionComponent<RecipeBoxProps> = ({ bucketId, recipe, onDelete }) => {

    const [{ isDragging }, drag] = useDrag({
        item: { type: "recipe", id: recipe.getId(), from: bucketId},
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging()
        })
    })

    const c = new Intl.Collator();
    const labels = recipe.getLabels()
        .sort(c.compare)
        .map(n => <Col key={n} sm="auto" className="mealee-bucker-recipe-label mt-1 ml-2">{n}</Col>)

    return (
        <div
            ref={drag}
            className="hover"
            style={{
                position: 'relative',
                width: '100%',
                height: '100%',
                opacity: isDragging ? 0.5 : 1,
                cursor: 'move',
            }}>

            <Card className="mealee-bucket-recipe">
                <Card.Body className="mealee-bucket-recipe-body">
                <Container>
                <Row>
                    <Col className="pl-0 ml-2">
                        {recipe.getTitle()}
                    </Col>
                </Row>
                <Row>
                    {labels}
                </Row>
                </Container>
                </Card.Body>
            </Card>

            <div
                className="hover-on"
                style={{
                    position: 'absolute',
                    top: 0,
                    right: 0,
                    height: 40,
                    width: 20,
                    zIndex: 1,
                    opacity: 0.5
                }}>

                <Trash height="18" onClick={() => onDelete(recipe.getId())} style={{ cursor: 'pointer' }} />
                <OverlayTrigger
                    placement="auto"
                    overlay={
                        <Popover id="popover-basic">
                            <Popover.Title as="h4">{recipe.getTitle()}</Popover.Title>
                            <Popover.Content>
                                <RecipeQuickView recipe={recipe} />
                            </Popover.Content>
                        </Popover>}>
                    <Eye height="18" style={{ cursor: 'pointer' }} />
                </OverlayTrigger>

            </div>
        </div>
    )
}

type BucketProps = {
    bucketId: number,
    recipes: Recipe[],
    onDelete: (bucketid: number, recipeid: string) => void
}

const Bucket: FunctionComponent<BucketProps> = ({ bucketId, recipes, onDelete }) => {

    return (
        <Card className="mealee-bucket">
            <Card.Header className="mealee-bucket-header">{"Day " + (bucketId + 1)}</Card.Header>
            <Card.Body className="mealee-bucket-body">
                {recipes.map(r => <RecipeBox key={r.getId()} bucketId={bucketId} recipe={r}
                                        onDelete={(recipeId) => onDelete(bucketId, recipeId)} />)}
            </Card.Body>
        </Card>
    )
}

type DroppableBucketProps = BucketProps & {
    onDrop: (buckedId: number, recipeId: string) => void,
    onMove: (from: number, to: number, recipeId: string) => void
}

const DroppableBucket: FunctionComponent<DroppableBucketProps> = ({ bucketId, recipes, onDrop, onMove, onDelete }) => {

    const [{ isOver }, drop] = useDrop({
        accept: "recipe",
        drop: (r) => {
            let r2 = r as unknown as { id: string, from?: number };
            if(r2.from !== undefined)
            {
                onMove(r2.from, bucketId, r2.id)
            } 
            else
            {
                onDrop(bucketId, r2.id)
            }
        },
        collect: monitor => ({
            isOver: !!monitor.isOver(),
        }),
    })

    return (
        <div
            ref={drop}
            style={{
                position: 'relative',
                width: '100%',
                height: '100%',
            }}
        >
            <Bucket bucketId={bucketId} recipes={recipes} onDelete={onDelete}></Bucket>

            {isOver && (
                <div
                    style={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        height: '100%',
                        width: '100%',
                        zIndex: 1,
                        opacity: 0.5,
                        backgroundColor: 'yellow',
                    }} />
            )}

        </div>
    )
}

type PlannerProps = {
    recipeDB: RecipeIndex,
    plan: Plan2,
    updatePlan: (plan: Plan2) => void
}

export const Planner: FunctionComponent<PlannerProps> = ({ recipeDB, plan, updatePlan }) => {

    const addRecipe = (bucketIdx: number, recipeId: string) => {
        updatePlan(plan.addRecipeId(bucketIdx, recipeId))
    }

    const deleteRecipe = (bucketIdx: number, recipeId: string) => {
        updatePlan(plan.deleteRecipeId(bucketIdx, recipeId))
    }

    const moveRecipe = (from: number, to: number, recipeId: string) => {
        updatePlan(plan.deleteRecipeId(from, recipeId).addRecipeId(to, recipeId))
    }

    let cols = _.range(plan.getNrBuckets()).map(i => {
        return (
            <Col key={i} xs="auto" className="pr-2 pl-0">
                <DroppableBucket
                    bucketId={i}
                    recipes={recipeDB.getAll(plan.getRecipeIds(i))}
                    onDrop={addRecipe}
                    onDelete={deleteRecipe}
                    onMove={moveRecipe} />
            </Col>
        )
    })

    return (
        <Container className="pt-0 pb-2 pl-0 pr-0">
            <Row>
                {cols}
            </Row>
        </Container>
    );

}
