import { Recipe } from "../model/Recipe";

import { RandomFilter } from "./RandomFilter";
import { MinIngredientsFilter } from "./MinIngredientsFilter";
import { MaxReuseFilter } from "./MaxReuseFilter";

export const randomFilter = new RandomFilter();

export interface Filter {
  id: string;
  name: string;
  filter: (
    explicitRecipes: Recipe[],
    selectedRecipes: Recipe[],
    remainingRecipes: Recipe[]
  ) => Recipe[];
}

export type Filters = {
  all: () => Filter[];
  get: (id: string) => Filter | undefined;
};

function createFilters() {
  const filters = [
    new MinIngredientsFilter(),
    new MaxReuseFilter(),
    randomFilter,
  ];
  const filterMap = new Map<string, Filter>(filters.map((f) => [f.id, f]));

  return {
    all: () => filters,
    get: (id: string) => filterMap.get(id),
  };
}

export const filters: Filters = createFilters();
