import { List, Set } from "immutable";

export class Plan2 {

  // Don't store Recipes, only ids. They are immutable, so if they change...
  private buckets = List<Set<string>>();

  private constructor(buckets: List<Set<string>>) {
    this.buckets = buckets;
  }

  public static new = (nrBuckets: number) => {
    return new Plan2(List().setSize(nrBuckets));
  };

  public setNrBuckets(nrBuckets: number) {
    return new Plan2(this.buckets.setSize(nrBuckets));
  }

  public getNrBuckets() {
    return this.buckets.size;
  }

  public getNrRecipes() {
    let ret = 0;
    for (let bucket of this.buckets) {
        if (bucket) {
            ret += bucket.count();
        }
    }
    return ret;
  }

  public addRecipeId(bucketNr: number, recipeId: string) {
    let bucket = this.buckets.get(bucketNr) ?? Set();
    return new Plan2(this.buckets.set(bucketNr, bucket.add(recipeId)));
  }

  public deleteRecipeId(bucketNr: number, recipeId: string) {
    let bucket = this.buckets.get(bucketNr) ?? Set();
    return new Plan2(this.buckets.set(bucketNr, bucket.delete(recipeId)));
  }

  public getRecipeIds(bucketNr: number) {
    let bucket = this.buckets.get(bucketNr) ?? Set();
    return Array.from(bucket);
  }

  public getAllRecipeIds(deDuplicate: boolean = true) {
    if(deDuplicate)
    {
        let ids = Set<string>();
        for (let bucket of this.buckets) {
            if (bucket) {
                ids = ids.concat(bucket);
            }
        }
        return Array.from(ids);
    }
    else
    {
        let ids = List<string>();
        for (let bucket of this.buckets) {
          if (bucket) {
            ids = ids.concat(bucket);
          }
        }
        return Array.from(ids);
    }
  }
}
