import { Filter } from "./Filter";
import { Recipe } from "../model/Recipe";

function getScore(ingredientsAlreadyUSed: Set<string>, recipe: Recipe) {
  return recipe.getIngredients().reduce((sum, i) => {
    if (ingredientsAlreadyUSed.has(i.getName())) {
      return sum;
    } else {
      return sum + 1;
    }
  }, 0);
}

export class MinIngredientsFilter implements Filter {
  public name = "Minimal number of ingredients";
  public id = "miningredients";
  public filter = (
    explicitRecipes: Recipe[],
    selectedRecipes: Recipe[],
    remainingRecipes: Recipe[]
  ) => {
    const ingredientsAlreadyUsed = new Set<string>(
      explicitRecipes
        .concat(selectedRecipes)
        .flatMap((r) => r.getIngredients().map((i) => i.getName()))
    );

    const scoredRecipes = remainingRecipes
      .map((r) => ({ recipe: r, score: getScore(ingredientsAlreadyUsed, r) }))
      .sort((a, b) => {
        if (a.score < b.score) {
          return -1;
        } else if (a.score > b.score) {
          return 1;
        } else {
          return 0;
        }
      });

    const result: Recipe[] = [];

    if (scoredRecipes.length > 0) {
      for (let scoredRecipe of scoredRecipes) {
        if (scoredRecipes[0].score === scoredRecipe.score) {
          result.push(scoredRecipe.recipe);
        } else {
          break;
        }
      }
    }

    return result;
  };
}
